.footer {
  width: 100%;
  height: 110px;
  position: absolute;
  bottom: 0;
  /* original dark grey rgb(34, 38, 41) */
  background-color: rgba(0, 0, 0, 1);
  font-family: 'Share', serif, Segoe UI, Frutiger, Frutiger Linotype;
  font-weight: 500;
}

.footer > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  color: #FFF;
  width: 80%;
  margin: 10px auto 0;
  padding: 25px 0px 20px 0px;
  bottom: 0;
  overflow: hidden;
}

.footer-name {
  float: left;
  width: 20%;
  font-size: 18px;
  font-family: 'Share', serif, Segoe UI, Frutiger, Frutiger Linotype;
  margin-top: -20px;
}

.footer-credit {
  font-size: 13px;
  margin-top: -10px;
}

.footer-credit > a {
  color: #FFF;
  text-decoration: none;
}

.footer-credit > a:hover {
  color: #D0D0D0;
}

.footer nav {
  float: left;
  width: 70%;
  text-align: center;
  margin: 0 -5%;
}

.footer nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer nav li {
  display: inline-block;
  padding: 0px 40px;
  position: relative;
}

.footer-links {
  color: #FFF;
  text-decoration: none;
  font-size: 24px;
}

.footer-links:hover {
  color: #D0D0D0;
}

.footer-icons {
  float: left;
  width: 10%;
  text-align: right;
  margin: 0 5%;
}

/* used to be 1240px before credit line was added */
@media (max-width: 1520px) {
  .footer {
    height: 160px;
  }

  .footer-name {
    float: left;
    width: 50%;
    text-align: center;
    -ms-flex-order: 1;
        order: 1;
  }

  .footer nav {
    float: left;
    width: 100%;
    -ms-flex-order: 2;
        order: 2;
    margin: 0;
  }

  .footer nav ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .footer nav li {
    padding: 10px 30px 0px 0px;
  }

  .footer-icons {
    float: left;
    width: 50%;
    text-align: center;
    -ms-flex-order: 1;
        order: 1;
    margin: 0;
  }
}

@media (max-width: 640px) {
  .footer {
    height: 220px;
    margin-top: 50px;
  }

  .footer > div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .footer-name {
    float: left;
    width: 100%;
    text-align: center;
    -ms-flex-order: 3;
        order: 3;
  }

  .footer nav {
    float: left;
    width: 100%;
    -ms-flex-order: 1;
        order: 1;
    margin: -20px auto 0px;
  }

  .footer nav ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
  }

  .footer nav li {
    padding: 15px 0px 25px 25px;
  }

  .footer-icons {
    float: left;
    width: 100%;
    text-align: center;
    -ms-flex-order: 2;
        order: 2;
    padding-bottom: 25px;
  }
}



